<template>
  <div id="header" @click="showDetail">
        <div class="content">
                <img :src="seller.avatar">
                <div class="text">
                    <div class="title">
                        <img src="../assets/imgs/brand.png">
                        <span>{{seller.name}}</span>
                    </div>
                    <div class="description">
                        {{seller.description}}/{{seller.deliveryTime}}后送达
                    </div>
                        <div class="decrease" v-if="seller.supports">
                            <img src="../assets/imgs/decrease.png" v-if="seller.supports[i].type==0">
                            <img src="../assets/imgs/discount.png" v-if="seller.supports[i].type==1">
                            <img src="../assets/imgs/guarantee.png" v-if="seller.supports[i].type==4">
                            <img src="../assets/imgs/invoice.png" v-if="seller.supports[i].type==3">
                            <img src="../assets/imgs/special.png" v-if="seller.supports[i].type==2">
                            <span  v-if="seller.supports[i]">{{seller.supports[i].description}}</span>
                        </div>
                </div>
                <div class="detail" v-if="seller.supports" @click="showDetail">
                    {{seller.supports.length}}个
                    <span>></span>
                </div>
        </div> 
        <div class="bottom">
            <img src="../assets/imgs/bulletin.png" >
            <span>{{seller.bulletin}}</span>
            <span class="go">></span>
        </div>  
  </div>
</template>

<script>
import { mapGetters , mapActions} from "vuex"
export default {
    data:function(){
        return{
            i:0
        }
    },
    computed:{
        ...mapGetters([
            "seller",
            "imgUrl"
        ])
    },
    updated(){
        document.getElementById("header").style.backgroundImage="url("+this.$store.getters.imgUrl+")"
        
            setTimeout(()=>{
                if(this.i == this.$store.getters.seller.supports.length-1){
                    this.i = 0;
                 }
                this.i++;
            },5000)
        },
    methods:{
        ...mapActions([
            "showDetail"
        ])
    }
}
</script>
<style scoped lang="less"> 
    #header{
        width: 100%;
        height: 2.67rem;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        .content{
            display: flex;
            width: 100%;
            height: 1.63rem;
            padding: 0.48rem 0.12rem 0 0.48rem;
            background: rgba(0, 0, 0, 0.4);
            img{
            width: 1.28rem;
            height: 1.28rem;
            border-radius: 0.04rem;
            }
            .text{
                padding: 0 0.32rem 0 0.32rem;
                .title{
                    font-size:0.32rem;
                    color: #ffffff;
                    font-weight: bold;
                    line-height: 0.36rem; 
                    img{
                    width: 0.6rem;
                    height: 0.36rem;
                    vertical-align: middle; 
                    }
                }
                .description{
                    margin-top: 0.16rem;
                    font-size: 0.24rem;
                    color: #ffffff;
                    font-weight: 200;
                    line-height: 0.24rem;                    
                }
                
                .decrease{
                    white-space:nowrap; 
                    overflow:hidden; 
                    text-overflow:ellipsis;
                                        
                    width: 3rem;
                    margin-top:0.2rem; 
                    font-size: 0.2rem;
                    color:#ffffff;
                    font-weight: 200;
                    line-height: 0.25rem;
                    img{
                        vertical-align: middle; 
                        width: 0.25rem;
                        height: 0.25rem;
                    }
                }
            }
            .detail{
                    font-size: 0.2rem;
                    color: #ffffff;
                    font-weight: 200;
                    line-height: 0.48rem;
                    text-align: center;
                    height: 0.48rem;
                    width: 0.85rem;
                    border-radius: 0.24rem;
                    background: rgba(0, 0, 0, 0.2);
                    margin-top: 1.1rem;
            }
        }
        .bottom{
                height: 0.56rem;
                width: 100%;
                background: rgba(0, 0, 0, 0.6);
                img{
                    width: 0.45rem;
                    height: 0.24rem;
                    float: left;
                    margin-top: 0.16rem;
                    margin-left: 0.24rem
                }
                span{
                    margin-left: 0.08rem;
                    display: block;
                    float: left;
                    width: 6.2rem;
                    font-size: 0.2rem;
                    color: #ffffff;
                    font-weight: 200;
                    line-height: 0.56rem;
                    white-space:nowrap; 
                    overflow:hidden; 
                    text-overflow:ellipsis; 
                }
                .go{
                    margin-left: 0.08rem;
                    width: 0.2rem;
                    display: block;
                    float: left; 
                }
        }
    }
</style>


