<template>
    <div class="add">
        <transition enter-active-class="animated rotateInDownRight" leave-active-class="animated rotateOutUpRight">
        <img src="../assets/imgs/minus.png" v-show="food.count > 0" @click="doMinus()" class="do-minus">
        </transition>
        <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <span v-show="food.count > 0">{{food.count}}</span>
        </transition>
        <img src="../assets/imgs/add.png" @click="doAdd()" class="do-add">
    </div>
</template>

<script>
import {mapActions , mapGetters} from "vuex";
import Vue from 'vue';
export default {
  props:[
      "food"
  ],
  methods:{
     doAdd(){
         if(this.food.count){
             this.food.count++;
             return
         }
         Vue.set(this.food,"count",1); 
     },
     doMinus(){
         this.food.count--;
     }
  },
  mounted(){
     
  }
}
</script>

<style lang="less" scoped>
    .add{
        font-size: 0.2rem;
        color: rgb(147,153,159);
        word-spacing: 0.2rem;
        line-height: 0.46rem;
        .do-add{
            float: right;
            width: 0.42rem;
            height: 0.42rem;

        }
        span{
            padding-left: 0.24rem;
            float: left;
        }
        .do-minus{
            float: left;
            width: 0.39rem;
            height: 0.39rem;
        }
        img{
            width: 0.4rem;
            height: 0.4rem;
            vertical-align:text-top;
        }
    }
</style>



