<template>
    <div id="content">
        <div class="nav">
            <router-link to="/goods" activeClass="now">商品</router-link> 
            <router-link to="/ratings" activeClass="now">评价</router-link> 
            <router-link to="/seller" activeClass="now">商家</router-link> 
        </div>
        <div class="content">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
        
    </div>
</template>

<script>
export default {
    watch:{
        $route(n,o){
            if(n.path=="/ratings"||n.path=="ratings"){
                setTimeout(() =>{
                   this.$store.dispatch("getRatings"); 
                }, 20); 
            }

        }
    }
}  
</script>

<style lang="less" scoped>
    #content{
        .nav{
            background: #ffffff;
            width: 100%;
            display: flex;
            height: 0.8rem;
            background-color: #ffffff;
            border-bottom: 1px solid rgba(7,17,27,0.1);
            align-items: center;
            justify-content: space-around;
            a{
                display: block;
                font-size: 0.28rem;
                color: rgb(77, 85, 93);
                line-height: 0.28rem;
            }
            .now{
                color: rgb(240,20,20);
            }
            .content{
                width: 100%;
            }
        }
    }
</style>



