import types from '../types';
import axios from 'axios';
import Vue from 'vue';
let state = {
    ratings:{}
};

let getters = {
    ratings(){
        return  state.ratings;
    }
};

let actions = {
    getRatings({commit,state}){
        axios.get("./data.json").then((res)=>{
            // if(res.data.errno == 0){
                commit(types.GET_RATINGS,res.data.ratings)
            // }
        })
        
    },

};

let mutations = {
    [types.GET_RATINGS](state,data){
        state.ratings = data;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}


