import Vue from 'vue'
import Router from 'vue-router'
import goods from '../components/goods.vue'
import seller from '../components/seller.vue'
import ratings from '../components/ratings.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/goods',
      name: 'goods',
      component: goods
    },
    {
      path: '/seller',
      name: 'seller',
      component: seller
    },
    {
      path: '/ratings',
      name: 'ratings',
      component: ratings
    },
    {
      path: '*',
      redirect:"/goods",
      component: goods
    }
  ]
})



