<template>
    <div id="detail">
        <div class="bg"></div>
        <div class="bottom">
            <div class="title">
                <span>购物车</span>
                <span class="do-empty" @click="doEmpty()" > 清空</span>
            </div>
            <div ref="carts" class="box">
                <div class="content" >
                    <div v-if="selectFoods.length==0" class="empty">
                         购物车中空空如也
                    </div>
                    <div v-if="selectFoods" >
                        <div  v-for="(item,key) in selectFoods" :key="key" class="item">
                            <div class="name">{{item.name}}</div>
                            <div class="do-add">
                                <add :food="item"></add>
                            </div>
                            <div class="price">¥{{item.price*item.count}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  
</template>

<script>
import {mapGetters , mapActions} from "vuex";
import add from "./add";
import Vue from 'vue';
import BScroll from 'better-scroll';
export default {
    ports:[
        "goods"
    ],
    components:{
        "add":add
    },
    computed:{
        ...mapGetters([
        "goods"
        ]),
        selectFoods(){
            let foods = [];
            for(let i= 0; i<this.goods.length;i++){
                for(let j=0; j<this.goods[i].foods.length; j++){
                    if(this.goods[i].foods[j].count>0){
                        foods.push(this.goods[i].foods[j]);
                    
                    }
                }
            }
            return foods
        }
    },
   methods:{
        setBScroll(){
            let scroll_1 = new BScroll(this.$refs.carts,{
                "click":true
            });
        },
        doEmpty(){
            this.goods.forEach(good=>{
                good.foods.forEach(food=>{
                    food.count=0;
                })
            });
            this.$store.dispatch("showCarts")
        }
    },
    mounted(){
        Vue.nextTick(()=>{
            this.setBScroll();
        }); 
    },
    watch:{
        goods(){
            setTimeout(()=> {
                this.scroll_1.refresh();
            }, 300);
        }
    }
}
</script>

<style lang="less" scoped>
    #detail{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        color: #ccc;
        font-size: 0.3rem;
        background: rgba(0, 0, 0, 0.5);
        bottom: 0rem;
        z-index: 10000;
        .bg{
            position: fixed;
            bottom: 0;
            height: 2rem;
            width: 100%;
            background: #ffffff;
        }
        .bottom{
            background: #ffffff;
            width: 100%;
            position: absolute;
            bottom: 0.95rem;
            .title{
                height: 0.8rem;
                background: #f3f5f7;
                font-size: 0.28rem;
                font-weight: 200;
                color: rgb(7,17,27);
                line-height: 0.8rem;
                display: flex;
                padding-left: 0.36rem;
                padding-right: 0.36rem;
                justify-content: space-between;
                .do-empty{
                    color: rgb(0,160,220);
                }
            }
            .box{
                overflow: hidden;
                max-height: 4rem;
                .content{
                    padding-left: 0.36rem;
                    padding-right: 0.36rem;
                    .item{
                        line-height: 0.96rem;
                        height: 0.96rem;
                        border-bottom: 1px solid rgba(7,17,27,0.1);
                        font-size: 0.28rem;
                        color: rgb(7,17,27);
                        .name{
                            float: left;
                        }
                        .price{
                            float: right;
                            color: rgb(240,0,0);
                            font-weight: 700;
                        }
                        .do-add{
                            margin-left: 0.2rem;
                            margin-top: 0.31rem;
                            width: 1.4rem;
                            float: right;
                        }

                    }
                }
            }
            .empty{
                text-align: center;
                font-size: 0.3rem;
                line-height: 1rem;
            }
        }
    }
</style>



