<template>
  <div id="cart" @click="showCarts">
        <div class="icon low" v-if="foodsPrice<20">
            <img src="../assets/imgs/cart.png">
            <div class="flag" v-if="count>0">{{count}}</div>
        </div>
        <div class="icon heigh" v-if="foodsPrice>=20">
            <img src="../assets/imgs/cart.png">
            <div class="flag" v-if="count>0">{{count}}</div>
        </div>
        <div class="price">
            ¥{{foodsPrice}}
        </div>
        <div class="extra">
            另需配送费¥4元
        </div>
        <div class="pay low" v-if="foodsPrice==0">
            ¥20起送
        </div>
        <div class="pay low" v-if="foodsPrice < 20 && foodsPrice > 0">
            还差¥{{seller.minPrice-foodsPrice}}起送
        </div>
        <div class="pay heigh" v-if="foodsPrice>=20">
            确认购买
        </div>
  </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex';
export default {
    props:[
            
        ],
    computed:{
        ...mapGetters([
            "seller",
            "goods"
        ]),
        selectFoods(){
            let foods = [];
            for(let i= 0; i<this.goods.length;i++){
                for(let j=0; j<this.goods[i].foods.length; j++){
                    if(this.goods[i].foods[j].count>0){
                        foods.push(this.goods[i].foods[j]);
                    
                    }
                }
            }
            return foods
        },
        foodsPrice(){
            let price = 0;
            for(let i =0; i<this.selectFoods.length; i++){
                price+= (this.selectFoods[i].price * this.selectFoods[i].count)
            }
            return price
        },
        count(){
            let count = 0;
            if(this.goods=={}){
                this.goods=[];
            }
            this.goods.forEach( (good) =>{
                good.foods.forEach( (food) =>{
                    if(food.count>0){
                        count+= food.count;
                    }
                });
            });
            return count;
        }
    },
    methods:{
        ...mapActions([
            "showCarts"
        ]),
        
    }
}
</script>

<style lang="less" scoped>
    #cart{
        height: 0.95rem;
        width: 100%;
        display: flex;
        align-items: center;
        position: fixed;
        bottom: 0;
        z-index: 11111;
        background-color: #141d27;
        font-size: 0.24rem;
        color: rgba(255, 255, 255, 0.4);
        font-weight: 700;
        line-height: 0.48rem;
        .icon{
            flex-shrink: 0;
            border: 0.12rem solid #141d27;
            text-align: center;
            line-height: 0.88rem;
            width: 0.88rem;
            height: 0.88rem;
            border-radius: 0.88rem;
            margin-top: -0.24rem;
            margin-left: 0.24rem;
            >img{
                margin-top: 0.1rem;
                line-height: 0.88rem;                
                width: 0.66rem;
                height: 0.66rem;
            }
            &.low{
                background: #ccc;
            }
            &.heigh{
                background: #FF9900; 
            }
            .flag{
                color: #fff;
                position: absolute;
                width: 0.4rem;
                height: 0.4rem;
                text-align: center;
                line-height: 0.4rem;
                left: 1rem;
                top: -0.2rem;
                border-radius: 0.2rem;
                background: rgb(255,0,0);
                z-index: 1000000;
            }
        }
        .price{
            margin-left: 0.36rem;
            font-size: 0.32rem;
            font-weight: 700;
            line-height: 0.48rem;
        }
        .extra{
            margin-left: 0.24rem;
            padding-left: 0.24rem;
            border-left: 0.01rem solid rgba(255, 255, 255, 0.1);
            height: 0.48rem;
            font-size: 0.2rem;
            line-height: 0.48rem;
            width: 2.1rem;
        }
        .pay{
            margin-left: 1.1rem;
            width: 2.1rem;
            height: 100%;
            line-height: 0.95rem;
            font-size: 0.24rem;
            font-weight: 700;
            text-align: center;
            &.heigh{
                background: #FF9900;
                color: #ffffff;
            }
        }
    }
</style>



