<template>
    <div ref="food" class="food">
        <div id="food-detail">
            <img :src="food.image" @click="foodDetail">
            <div class="title">
                <div class="left">
                    <div class="name">
                        {{food.name}}
                    </div>
                    <div class="title-ratings">
                        月售{{food.sellCount}}份
                        好评率{{food.rating}}％
                    </div>
                    <div class="price">
                        <span>¥{{food.price}}</span>
                        <span class="old-price" v-if="food.oldPrice">¥{{food.oldPrice}}</span>
                    </div>
                </div>
                <div class="right">
                    <div @click="doAdd()" v-if="checked" class="carts">加入购物车</div>
                    <add :food="food" v-if="!checked"></add>
                </div>
                <div class="clear"></div>
            </div>
            <div class="info">
                <div class="head">
                    商品详情
                </div>
                <div class="introduce">
                    <div v-if="!food.info">
                        该商品无介绍
                    </div>
                    <div v-if="food.info">
                        {{food.info}}。
                    </div>
                </div>
            </div>
            <div class="ratings">
                <div class="head">
                    商品评价
                </div>
                <div class="num">
                    <div class="all" @click="changeAll('all')">
                        全部{{food.ratings.length}}
                    </div>
                    <div class="good" @click="changeAll('good')">
                        推荐{{good.length}}
                    </div>
                    <div class="bad" @click="changeAll('bad')">
                        吐槽{{bad.length}}
                    </div>
                </div>
                <div class="choose">
                    <img src="../assets/imgs/unchoose.png" @click="doChoose()" v-if="flag">
                    <img src="../assets/imgs/choose.png" @click="doChoose()" v-if="!flag">
                    <span>只看有内容的评价</span> 
                </div>
            </div>
            <div class="ratings-content" v-if="all.length">
                <div v-for="(item,key) in all" :key="key" class="item"> 
                    <div class="date">
                        <span>
                            {{new Date(item.rateTime).toLocaleString()}}
                        </span>
                        <span class="user">
                            {{item.username}}
                            <img :src="item.avatar">
                        </span>
                    </div>
                    <div class="content">
                        <img src="../assets/imgs/good.png" v-if="item.rateType==0">
                        <img src="../assets/imgs/bad.png" v-if="item.rateType==1">
                        <span v-if="item.text">{{item.text}}</span>
                        <span v-if="!item.text">
                            该用户未填写评价
                            <span v-if="item.rateType==0">系统默认好评</span>
                        </span>
                    </div>
                </div>
            </div>
            <div class="show" v-if="all.length==0">
                评论区空空如也！
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters , mapActions} from 'vuex';
    import Vue from 'vue';
    import BScroll from 'better-scroll'
    import add from './add.vue'
export default {
    data:function(){
        return{
            good:[],
            bad:[],
            all:[],
            flag:true,
            hasContent:[]
        }
    },
    components:{
        "add":add
    },
    computed:{
      ...mapGetters([
          "food",
      ]),
      checked(){
          if(!this.food.count){
              return true
          }else{
              return false
          }
      }
    },
    mounted(){
        Vue.nextTick(()=>{
            setTimeout(()=>{
                this.setBScroll();
            },100)     
        }); 
        this.cals();
    },
    methods:{
      ...mapActions([
          "foodDetail"
      ]),
      setBScroll(){
            this.scroll_1 = new BScroll(this.$refs.food,{
                "click":true
            });
        },
        cals(){
            let food = this.$store.getters.food;
            this.all = food.ratings;
            // for(let i=0; i<food.ratings.length;i++){
            //     if(food.ratings[i].text){
            //         this.hasContent.push(food.ratings[i]);
             
            //     }
            // }
            this.good = [];
            this.bad = [];
            for(let i=0; i<food.ratings.length;i++){
                if(food.ratings[i].rateType==0){
                    this.good.push(food.ratings[i]);
                    continue
                }
                    this.bad.push(food.ratings[i]);
            }
        },
        doChoose(){
            this.flag=!this.flag;
            if(!this.flag){
                this.hasContent = [];
                this.all.forEach(item=>{
                    this.hasContent.push(item);
                });
                for(let i=0; i<this.all.length;i++){
                    if(this.all[i].text==""){
                        this.all.splice(i,1);
                    }
                }
                return;
            }
            this.all = [];
            this.hasContent.forEach(item=>{
                    this.all.push(item);
            });
        },
        changeAll(flag){
            this.scroll_1.refresh();
            if(flag=='all'){
                this.all = this.$store.getters.food.ratings;
                return
            }
            if(flag=='good'){
                this.all=this.good;
                return
            }
            this.all=this.bad
        },
        doAdd(){
            Vue.set(this.food,"count",1)
        }
    }
}
</script>

<style lang="less" scoped>
    .food{
        width: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0.95rem;
        z-index: 11;
        #food-detail{
        background-color: #f3f5f7;
        >img{
            width: 100%;
            height: 7.5rem;
        }
        .title{
            background: #ffffff;
            padding: 0.36rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            .left{
                float: left;
                .name{
                font-size: 0.32rem;
                font-weight: 700;
                color: rgb(7, 17,27);
                line-height: 0.28rem;
                padding-bottom: 0.16rem;
                }
                .title-ratings{
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: 0.2rem;
                    padding-bottom: 0.36rem;
                }
                .price{
                    font-size: 0.28rem;
                    font-weight: 700;
                    color: rgb(240,20,20);
                    line-height: 0.48rem;
                    .old-price{
                        text-decoration-line: line-through;
                        font-size: 0.2rem;
                        font-weight: 700;
                        color: rgb(147,153,159);
                        line-height: 0.48rem;
                    }
                }
            }
            .right{
                float: right;
                width: 1.48rem;
                height: 0.48rem;
                font-size: 0.2rem;
                color: #ffffff;
                line-height: 0.48rem;
                margin-top: 0.9rem;
                .carts{
                    border-radius: 0.24rem;
                    background-color: rgb(0,160,220);
                    text-align: center;
                }

            }
            .clear{
                clear: both;
            }
        }
            .info{
                padding: 0.36rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                border-top: 0.01rem solid rgba(7,17,27,0.1);
                margin-top: 0.32rem;
                background: #ffffff;
                .head{
                    font-size: 0.3rem;
                }
                .introduce{
                    padding-top: 0.12rem;
                    font-size: 0.24rem;
                    font-weight: 200;
                    color: rgb(77,85,93);
                    line-height: 0.48rem;
                }
            }
            .ratings{
                margin-top: 0.32rem;
                padding: 0.36rem;
                background-color: #ffffff;
                border-bottom: 1px solid rgb(147,153,159);
                .head{
                    font-size: 0.3rem;
                }
                .num{
                    display: flex;
                    padding-top: 0.36rem;
                    padding-bottom: 0.36rem;
                    height: 0.64rem;
                    text-align: center;
                    line-height: 0.64rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .all{
                        margin-left: 0.12rem;
                        width: 1.2rem;
                        background-color: rgb(0,160,220);
                        color: #ffffff;
                        font-size: 0.24rem;
                        border-radius: 0.02rem;
                        
                    }
                    .good{
                        margin-left: 0.12rem;                        
                        width: 1.2rem;
                        background-color: rgba(0,160,220,0.2);
                        color: rgb(77,85,93);
                        font-size: 0.24rem;
                        border-radius: 0.02rem; 
                    }
                    .bad{
                        margin-left: 0.12rem;
                        
                        width: 1.2rem;
                        background-color: rgba(77,85,93,0.2);
                        color: rgb(77,85,93);
                        font-size: 0.24rem;
                        border-radius: 0.02rem; 
                    }
                }
                .choose{
                    padding-top: 0.24rem;
                    padding-bottom: 0.24rem;
                    font-size: 0.24rem;
                    color: rgb(147,153,159);
                    >img{
                        width: 0.42rem;
                        height: 0.42rem;
                        vertical-align: middle;
                    }
                    span{
                        margin-left: 0.08rem;
                    }
                    
                }
            }
            .show{
                line-height: 0.8rem;
                font-size: 0.4rem;
                background: #fff;
                text-align: center;
                color: rgb(7,17,27);
            }
            .ratings-content{
                padding-right: 0.36rem;
                padding-left: 0.36rem;
                background: #ffffff;
                font-size: 0.2rem;
                color: rgb(147,153,159);
                .item{
                    padding-top: 0.32rem;
                    padding-bottom: 0.32rem;
                    border-bottom: 1px solid rgba(77,85,93,0.2);
                    .date{
                        .user{
                            float: right;
                        
                            line-height: 0.24rem;
                            >img{
                                margin-left: 0.12rem;
                                width: 0.24rem;
                                height: 0.24rem;
                                border-radius: 0.24rem;
                            }
                        }
                    }
                    .content{
                        padding-top: 0.12rem;
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                        >img{
                            margin-right: 0.08rem;
                            width: 0.24rem;
                            height: 0.24rem;
                            vertical-align: middle;
                        }
                    }
                }
            }
        }
    }
</style>



