
<template>
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
    <div id="detail" @click="doQuit">
        <div class="title">
            <img :src="seller.avatar">
            <div class="shop-name">{{seller.name}}</div>
            <!-- 引人星星组件 -->
            <star :num="seller.score" :size="'big'"></star>
            <div class="price">起送¥{{seller.minPrice}}｜配送¥{{seller.deliveryPrice}}｜{{seller.deliveryTime}}分钟</div>
            <div class="price">配送时间：09:00-21:00</div>
        </div>
        <hr width="90%" style="height:1px;border:none;border-top:1px solid rgba(255,255,255,0.2);">
        <div class="discount" v-if="seller.supports">
            <div v-for="(item,k) in seller.supports" :key="k" class="item">
                <img src="../assets/imgs/decrease.png" v-if="item.type==0">
                <img src="../assets/imgs/discount.png" v-if="item.type==1">
                <img src="../assets/imgs/guarantee.png" v-if="item.type==4">
                <img src="../assets/imgs/invoice.png" v-if="item.type==3">
                <img src="../assets/imgs/special.png" v-if="item.type==2">
                <span>{{item.description}}</span>
            </div>
        </div>
        <hr width="90%" style="height:1px;border:none;border-top:1px solid rgba(255,255,255,0.2);">
        <div class="info">
            <div class="title">商家公告</div>
            <div class="content">
                {{seller.bulletin}}
            </div>
        </div>
    </div>
    </transition>
</template>

<script>
    import {mapActions,mapGetters} from 'vuex';
    import star from "./star.vue"
    export default{
        components:{
            star
        },
        computed:{
            ...mapGetters([
                "seller"
            ])
        },
        methods: {
            ...mapActions([
                "doQuit"
            ])
        }
    }
</script>

<style lang="less" scoped>
    #detail{
        position: absolute;
        top: 0.2rem;
        left: 0.2rem;
        right: 0.2rem;
        bottom: 0.2rem;
        background: rgba(7, 17, 27, 0.8);
        border-radius: 0.1rem;
        z-index: 200;
        padding-top: 1rem;
        .title{
            text-align: center;
            img{
                width: 1rem;
                height: 1rem;
            }
            .shop-name{
                margin-top: 0.32rem;
                margin-bottom: 0.32rem;                
                font-size: 0.32rem;
                font-weight: 700;
                color: #ffffff;
                line-height: 0.32rem;
            }
            .price{
                margin-top: 0.32rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: #ffffff;
                margin-bottom: 0.32rem;                                
            }
        }
        .discount{
            padding-left: 0.96rem;
            padding-top: 0.36rem;
            margin-bottom: 0.56rem;
            .item{
                margin-top: 0.12rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: #ffffff;
                line-height: 0.24rem;
                img{
                width: 0.35rem ;
                height: 0.35rem ;
                margin-right: 0.12rem;
                }
            }
        }
        .info{
            padding-top: 0.3rem;
            .title{
                text-align: center;
                font-size: 0.3rem;
                font-weight: 700;
                color: #ffffff;
                line-height: 0.3rem;
            }
            .content{
                margin: 0.2rem auto;
                text-align: left;
                width: 80%;
                font-size: 0.24rem;
                line-height: 0.48rem;
                font-weight: 200;
                color: #ffffff;
            }
        }
    }

</style>


