<template>
    <!-- 父容器 -->
    <div class="seller" ref="content">
        <!-- 绑定better的外层无样式容器 -->
        <div>
            <!-- 顶部 -->
            <div class="top">
                <!-- 商家名和评分容器 -->
                <div class="title">
                    <div class="name">
                        <div>{{seller.name}}</div>
                        <div class="star">
                            <star :num="seller.score" :show="1" :size="'big'"></star>
                            <div class="rating-count">({{seller.ratingCount}})</div>
                            <div class="sell-count">月售{{seller.sellCount}}单</div>
                        </div>
                    </div>
                    <div class="like">
                        <div @click="changeLike()">
                        <transition enter-active-class="animated bounceIn" leave-active-class="animated bounceIn"> 
                            <img src="../assets/imgs/dislike.png" v-if="!flag">
                            <img src="../assets/imgs/like.png" v-if="flag">
                        </transition>
                        </div>
                        <div class="liked" v-if="!flag">未收藏</div>
                        <div class="liked" v-if="flag">已收藏</div>
                    </div> 
                </div>
                <div class="rule"> 
                    <div class="min-price borr">
                        <div class="rule-title">起送价</div>
                        <div>
                            {{seller.minPrice}}<span>元</span>
                        </div>
                    </div>
                    <div class="delivery-time borr" >
                        <div class="rule-title">平均配送时间</div>
                        <div>
                            {{seller.deliveryTime}}<span>元</span>
                        </div>
                    </div>
                    <div class="delivery-price">
                        <div class="rule-title">配送费</div>
                        <div>
                            {{seller.deliveryPrice}}<span>元</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="bulletin">
                <div class="title">
                    公告和活动
                </div>
                <div class="content">
                    {{seller.bulletin}}
                </div>
                <div class="supports">
                    <ul>
                        <li v-for="(item,k) in seller.supports" :key="k">
                            <img src="../assets/imgs/decrease.png" v-if="item.type==0">
                            <img src="../assets/imgs/discount.png" v-if="item.type==1">
                            <img src="../assets/imgs/guarantee.png" v-if="item.type==4">
                            <img src="../assets/imgs/invoice.png" v-if="item.type==3">
                            <img src="../assets/imgs/special.png" v-if="item.type==2">
                            <span >{{item.description}}</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="seller-pics">
                <div class="pics-title">
                    商家实景
                </div>
                <div ref="pics" class="pics-box">
                    <div class="pics" ref="img">
                        <img :src="item" v-for="(item,k) in seller.pics" :key="k">
                    </div>
                </div>
            </div>
            <div class="infos">
                <div class="info-title">
                    商家信息
                </div>
                <div class="info-content">
                    <div class="item" v-for="(item,k) in seller.infos" :key="k">
                        {{item}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import star from './star.vue';
    import Vue from 'vue';
    import BScroll from 'better-scroll';
    export default {
        data(){
            return {
                flag : false
            }
        },
        components:{
            star
        },
        computed:{
            ...mapGetters([
                "seller"
            ])
        },
        mounted(){
            Vue.nextTick(()=>{
                setTimeout(()=> {
                    this.setBScroll();
                }, 20);
            })
        },
        methods:{
            setBScroll(){
                this.scroll_1 = new BScroll(this.$refs.content,{
                    "click":true
                })
                this.$refs.img.style.width = 140*this.seller.pics.length+5*(this.seller.pics.length-1)+"px";
                this.scroll_pics = new BScroll(this.$refs.pics,{
                    "scrollX":true,
                    "scrollY":false
                })
            },
            // 改变收藏样式
            changeLike(){
                this.flag = !this.flag
            }
        },
        watch:{
            seller(){
                this.$refs.img.style.width = 140*this.seller.pics.length+5*(this.seller.pics.length-1)+"px";
                console.log(this.$refs.img.style.width)
                this.scroll_pics = new BScroll(this.$refs.pics,{
                    "scrollX":true,
                    "scrollY":false
                })
            }
            
        }
    }
</script>

<style lang="less" scoped>
    .seller{
        background: #f3f4f5;
        width: 100%;
        position: absolute;
        bottom: 0.95rem;
        top:3.5rem;
        overflow: hidden;
        .top{
            padding: 0.36rem;
            background: #fff;
            border-bottom: 1px solid rgba(7,17,27,0.1);
             .title{
                display: flex;
                justify-content: space-between;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .name{
                    font-size: 0.28rem;
                    color: rgb(7,17,27);
                    .star{
                        padding-top: 0.16rem;
                        padding-bottom: 0.3rem;
                        display: flex;
                        font-size: 0.2rem;
                        color: rgb(77,85,93);
                        line-height: 0.36rem;
                        .rating-count{
                            padding-left: 0.16rem;
                            padding-right: 0.24rem;
                        }
                    }
                }
                .like{
                    text-align: center;
                    img{
                        height: 0.48rem;
                        width: 0.48rem;
                    }
                    .liked{
                        padding-top: 0.08rem;
                        font-size: 0.2rem;
                        color: rgb(77,85,93);
                        line-height: 0.2rem;
                    }
                }
            }
            .rule{
                padding-top: 0.36rem;
                padding-bottom: 0.36rem;
                font-size: 0.48rem;
                font-weight: 200;
                color: rgb(7,17,27);
                line-height: 0.48rem;
                display: flex;
                text-align: center;
                justify-content: space-around;
                width: 100%;
                >div{
                    flex: 1
                }
                .borr{
                    border-right: 1px solid rgba(7,17,27,0.1);
                }
                span{
                    font-size: 0.2rem;
                }
                .rule-title{
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                }
            }
        }
        .bulletin{
            margin-top: 0.36rem;
            background: #ffffff;
            border-top: 1px solid rgba(7,17,27,0.1);
            border-bottom: 1px solid rgba(7,17,27,0.1);
            padding: 0.36rem;
            .title{
                font-size: 0.28rem;
                color: rgb(7,17,27);
            }
            .content{
                padding: 0.24rem;
                color:rgb(240,20,20);
                font-weight: 200;
                font-size: 0.24rem;
                line-height: 0.48rem;
            }
            .supports{
                ul{
                    li{
                        padding: 0.24rem;
                        font-size: 0.24rem;
                        font-weight: 200;
                        color: rgb(7,17,27);
                        line-height: 0.5rem;
                        height: 0.5rem;
                        border-top: 1px solid rgba(7,17,27,0.1);
                        >img{
                            width: 0.32rem;
                            height: 0.32rem;
                            vertical-align: middle;
                        }
                    }
                }
            }
        }
        .seller-pics{
            background: #ffffff;
            margin-top: 0.32rem;
            padding: 0.36rem;
            .pics-title{
                font-size: 0.28rem;
                color: rgb(7,17,27);
            }
            .pics-box{
                overflow: hidden;
                width: 7rem;
                .pics{
                    margin-top: 0.24rem;
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    >img{
                        margin-right: 0.12rem;
                        width: 2.4rem;
                        height: 1.8rem;
                    }
                }
            }
        }
        .infos{
            font-size: 0.28rem;
            color: rgb(7,17,27);
            padding: 0.32rem;
            background: #ffffff;
            .info-content{
                margin-top: 0.2rem;
                font-weight: 200;
                line-height: 0.36rem;
                .item{
                    padding: 0.32rem 0.24rem;
                    border-top: 0.01rem solid rgba(7,17,27,0.1);

                }
            }
        }
        

    }
</style>


