<template>
    <div class="ratings" ref="ratings">
        <div class="box">
            <div class="top">
                <div class="left">
                    <div class="all-score">{{seller.score}}</div>
                    <div class="introduce">综合评分</div>
                    <div class="rankRate">高于周边商家{{seller.rankRate}}％</div>
                </div>
                <div class="right">
                    <div class="serviceScore">
                        <span>服务态度</span>
                        <div class="star">
                            <star :num="seller.serviceScore" :size="'middle'"></star>
                        </div>
                    </div>
                    <div class="foodScore">
                        <span>商品评分</span>
                        <div class="star">
                            <star :num="seller.foodScore" :size="'middle'"></star>
                        </div>                 
                    </div>
                    <div class="deliveryTime">
                        <span>送达时间</span>
                        <div class="time">
                            <span>{{seller.deliveryTime}}分钟</span>    
                        </div>                
                    </div>
                </div>
            </div>
            <div class="bottom">
                <div class="num">
                    <div class="all" @click="changeAll('all')">
                        全部{{seller.ratingCount}}
                    </div>
                    <div class="good" @click="changeAll('good')">
                        推荐{{good.length}}
                    </div>
                    <div class="bad" @click="changeAll('bad')">
                        吐槽{{bad.length}}
                    </div>
                </div>
                <div class="choose">
                    <img src="../assets/imgs/unchoose.png" @click="doChoose()" v-if="flag">
                    <img src="../assets/imgs/choose.png" @click="doChoose()" v-if="!flag">
                    <span>只看有内容的评价</span> 
                </div>
                <div class="ratings-content" v-if="all">
                    <div v-for="(item,key) in all" :key="key" class="item" v-if="flag || item.text"> 
                        <img :src="item.avatar" alt="">
                        <div class="user">
                            <div class="username">
                                <span >{{item.username}}</span>
                                <span class="date">{{new Date(item.rateTime).toLocaleString()}}</span>
                            </div>
                            <div class="star">
                                <star :num="item.score" :show="1" :size="'small'"></star>
                                <div v-if="item.deliveryTime" class="deliveryTime">{{item.deliveryTime}}分钟后送达</div>
                            </div>
                            <div class="content">
                                {{item.text}}
                                <span v-if="!item.text">
                                    该用户未填写评价
                                </span>
                            </div>
                            <div class="content-bottom" v-if="!item.recommend[0]==''">
                                    <img src="../assets/imgs/good.png" v-if="item.rateType==0">
                                    <img src="../assets/imgs/bad.png" v-if="item.rateType==1">
                               <div v-for="(i,k) in item.recommend" :key="k" class="item" v-if="k<4">
                                   {{i}}
                               </div>
                            </div>
                        </div>
                        <div></div>
                        
                    </div>
                </div>
                <div class="show" v-if="all.length==0">
                    评论区空空如也！
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import star from "./star.vue";
    import BScroll from "better-scroll";
    import Vue from 'vue';
    export default {
        components:{
            "star":star,
        },
        data:function(){
            return {
                "all":[],
                "bad":[],
                "good":[],
                "flag":true,
            }
        },
        computed:{
            ...mapGetters([
                "seller",
                'ratings'
            ])
        },
        created(){
            this.$store.dispatch("getRatings");
        },
        watch : {
            ratings () {
                this.$nextTick(()=>{
                    this.cals(); 
                    setTimeout(() =>{
                        if( !this.scroll_1 ){
                            this.scroll_1 = new BScroll(this.$refs.ratings,{
                                "click":true
                            });
                        }else{
                            this.scroll_1.refresh();
                        }
                    }, 30);
                })    
            }
        },
        methods:{
             cals(){
                    this.all = this.ratings;
                    this.good=[];
                    this.bad=[];
                    for(let i=0; i<this.all.length;i++){
                        if(this.all[i].rateType==0){
                            this.good.push(this.all[i]);
                            continue
                        }
                            this.bad.push(this.all[i]);
                    }
            },
            changeAll(key){
                setTimeout(()=> {
                    this.scroll_1.refresh();
                }, 300);
                if(key=='all'){
                    this.all = this.$store.getters.ratings;
                    return
                }
                if(key=='good'){
                    this.all=this.good;
                    return
                }
                this.all=this.bad
            },
            doChoose(){
                this.flag = !this.flag;
                setTimeout(()=> {
                    this.scroll_1.refresh();
                }, 300);
            }
        }
    }
</script>

<style lang="less" scoped>
    .ratings{
        background-color: #f3f5f7;
        width: 100%;
        position: absolute;
        bottom: 0.95rem;
        top:3.5rem;
        overflow: hidden;
        .box{
            .top{
                height: 2.3rem;
                background: #ffffff;
                padding: 0.36rem 0.48rem 0.36rem 0;
                display: flex;
                border-bottom: 1px solid rgba(17,27,67,0.1);
                .left{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    width: 2.75rem;
                    height: 70%;
                    text-align: center;
                    border-right: 1px solid rgba(17,27,67,0.1);
                    .all-score{
                        font-size: 0.48rem;
                        color: rgb(255,153,0);
                        line-height: 0.48rem;
                    }
                    .introduce{
                        font-size:0.24rem;
                        color: rgb(7,17,27);
                        line-height: 0.24rem ;
                    }
                    .rankRate{
                        font-size: 0.2rem;
                        color: rgb(122,122,27);
                        line-height: 0.2rem;
                    }
                }
                .right{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    height: 70%;
                    padding-left: 0.48rem;
                    font-size: 0.24rem;
                    line-height: 0.37rem;
                    color: rgb(7,17,27);
                    .serviceScore{
                        display: flex;
                        .star{
                            margin-left: 0.24rem;
                        }
                    }
                    .foodScore{
                        display: flex;
                        .star{
                            margin-left: 0.24rem;
                        }
                    }
                    .deliveryTime{
                        display: flex;
                        .time{
                            margin-left: 0.24rem;
                            font-size: 0.24rem;
                            color: rgb(147,153,159);
                        }
                    }
                }
            }
            .bottom{
                margin-top: 0.32rem;
                padding: 0.36rem;
                background-color: #ffffff;
                border-bottom: 1px solid rgb(147,153,159);
                .num{
                    display: flex;
                    padding-top: 0.36rem;
                    padding-bottom: 0.36rem;
                    height: 0.64rem;
                    text-align: center;
                    line-height: 0.64rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .all{
                        margin-left: 0.12rem;
                        width: 1.2rem;
                        background-color: rgb(0,160,220);
                        color: #ffffff;
                        font-size: 0.24rem;
                        border-radius: 0.02rem;
                        
                    }
                    .good{
                        margin-left: 0.12rem;                        
                        width: 1.2rem;
                        background-color: rgba(0,160,220,0.2);
                        color: rgb(77,85,93);
                        font-size: 0.24rem;
                        border-radius: 0.02rem; 
                    }
                    .bad{
                        margin-left: 0.12rem;
                        
                        width: 1.2rem;
                        background-color: rgba(77,85,93,0.2);
                        color: rgb(77,85,93);
                        font-size: 0.24rem;
                        border-radius: 0.02rem; 
                    }
                }
                .choose{
                    padding-top: 0.24rem;
                    padding-bottom: 0.24rem;
                    font-size: 0.24rem;
                    color: rgb(147,153,159);
                    >img{
                        width: 0.42rem;
                        height: 0.42rem;
                        vertical-align: middle;
                    }
                    span{
                        margin-left: 0.08rem;
                    }
                }
                .ratings-content{
                    .item{
                        padding-top: 0.36rem;
                        padding-bottom: 0.36rem;
                        display: flex;
                        border-bottom: 1px solid rgba(17,27,67,0.1);
                        >img{
                            width: 0.56rem;
                            height: 0.56rem;
                            border-radius: 0.56rem;
                        }
                        .user{
                            margin-left: 0.24rem;
                            .username{
                                font-size: 0.2rem;
                                color: rgb(7,17,27);
                                line-height: 0.24rem;
                                display: flex;
                                .date{
                                    margin-left: 2.4rem;
                                    color: rgb(147,153,159);
                                }
                                
                            }
                            .star{
                                margin-top: 0.12rem;
                                font-size: 0.2rem;
                                display: flex;
                                align-items: center;
                                font-size: 0.2rem;
                                font-weight: 200;
                                color: rgb(147,153,159);
                                .deliveryTime{
                                    padding-left: 0.12rem;
                                }
                            }
                            .content{
                                margin-top: 0.12rem;
                                font-size: 0.24rem;
                                color: rgb(7,17,27);
                                line-height: 0.36rem;
                               
                            }
                            .content-bottom{
                                margin-top: 0.22rem;
                                display: flex;
                                align-items: center;
                                 >img{
                                    width: 0.24rem;
                                    height: 0.24rem;
                                    vertical-align: middle;
                                }
                                .item{
                                    font-size: 0.18rem;
                                    height: 0.15rem;
                                    padding: 0.12rem;
                                    border: 0.01rem solid rgba(17,27,67,0.1);  
                                    margin-left: 0.12rem;
                                    text-align: center;
                                }
                            }
                        }
                    }
                }
                
            }
        }            
    }
</style>


