import types from '../types';
import axios from 'axios';

let state = {
    goods : [],
    cartsFlag : false,
    food : {},
    foodFlag : false
};

let getters = {
    goods(){
        return state.goods;
    },
    cartsFlag(){
        return state.cartsFlag;
    },
    food(){
        return state.food;
    },
    foodFlag(){
        return state.foodFlag;
    }
};

let actions = {
    getGoods({commit,state}){
        axios.get("./data.json").then((res)=>{
            // if(res.data.errno == 0){
                commit(types.GET_GOODS,res.data.goods)
            // }
        })
        
    },
    showCarts( { commit } ){
        commit(types.SHOW_CARTS)
    },
    foodDetail( { commit } ,data){
        commit(types.FOOD_DETAIL,data)
    }
};

let mutations = {
    [types.GET_GOODS](state,data){
        state.goods = data
    },
    [types.SHOW_CARTS](state){
        state.cartsFlag=!state.cartsFlag;
    },
    [types.FOOD_DETAIL](state,data){
        state.food = data;
        state.foodFlag = !state.foodFlag
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}




