<template>
    <div class="goods">
        <div class="menu-warp" ref="menu">
            <ul v-if="goods">
                <li v-for="(item,k) in goods" :key="k" @click="menuClick(k)" :class="{'client' : k == clientIndex}" class="menu-list-hook">
                    <div>
                        <img src="../assets/imgs/decrease.png" v-if="item.type==0">
                        <img src="../assets/imgs/discount.png" v-if="item.type==1">
                        <img src="../assets/imgs/guarantee.png" v-if="item.type==4">
                        <img src="../assets/imgs/invoice.png" v-if="item.type==3">
                        <img src="../assets/imgs/special.png" v-if="item.type==2">
                        {{item.name}}
                    </div>
                </li>
            </ul>  
        </div>
        <div class="foods-warp" ref="foods">
            <ul v-if="goods">
                <li v-for="(item,k) in goods" :key="k" class="food-list-hook">
                    <div class="title">
                        {{item.name}}
                    </div>
                    <div class="foods" v-for="(item,k) in item.foods" :key="k" >
                        <img :src="item.icon" @click="foodDetail(item)">
                        <div class="detail">
                            <div class="name">{{item.name}}</div>
                            <div class="description">{{item.description}}</div>
                            <div>
                                <span>月售{{item.sellCount}}份</span>  
                                <span>好评率{{item.rating}}%</span>  
                            </div>
                            <div class="price">
                                <span>¥{{item.price}} </span>
                                <span class="old-price" v-if="item.oldPrice">¥{{item.oldPrice}}</span>
                            </div>
                        </div>
                        <div class="add">
                            <add :food="item"></add>
                        </div>
                    </div>
                </li>
            </ul>    
        </div>
    </div>
</template>

<script>
    import {mapGetters} from "vuex";
    import add from "./add.vue";
    import BScroll from "better-scroll";
    import Vue from "vue";
    export default {
        computed: {
            ...mapGetters([
                "goods",
                "cartsFlag"                
            ]),
            clientIndex(){
                for(let i = 0; i < this.foodHeight.length; i++){
                    if(this.foodsY<this.foodHeight[i]-1){
                        return (i-1);
                    }
                   
                }
                return 0;
            }
        },
        components:{
            "add":add,
            
        },
        mounted(){
            Vue.nextTick(()=>{
                setTimeout(()=>{
                    this.setBScroll();
                    this.calsHeight();
                },500)     
            }); 
        },
        data:function(){
            return{
                "foodsY":0,
                "foodHeight":[0]
            }
        },
        watch:{
            foodsY(){
                let menuList = this.$refs.menu.getElementsByClassName("menu-list-hook");
                if(this.foodsY> this.foodHeight[this.foodHeight.length-2]){
                    this.scroll_1.scrollToElement(menuList[menuList.length-1],300);
                    return
                }
                if(this.foodsY<this.foodHeight[1]){
                    this.scroll_1.scrollToElement(menuList[0],300)
                    return
                }
            },
            cartsFlag(){
                this.carts =this.$store.getters.carts;
            }
            
        },
        methods:{
            setBScroll(){
                    this.scroll_1 = new BScroll(this.$refs.menu,{
                        "click":true,
                        "probeType":3 
                    });
                     this.scroll_2 = new BScroll(this.$refs.foods,{
                        "click":true,
                        "probeType":3                        
                    });
                    this.scroll_2.on("scroll",(pos)=>{
                        this.foodsY = Math.abs(Math.round(pos.y));
                    });
                },
            calsHeight(){
                let foodList = this.$refs.foods.getElementsByClassName("food-list-hook");
                let height = 0
                for(let i = 0 ; i<foodList.length; i++){
                    height += foodList[i].clientHeight
                    this.foodHeight.push(height)
                };
            },
            menuClick(k){
                let foodList = this.$refs.foods.getElementsByClassName("food-list-hook");
                this.scroll_2.scrollToElement(foodList[k],300)
            },
            foodDetail(food){
                this.$store.dispatch("foodDetail",food)
            }

        }
    }
</script>

<style lang="less" scoped>
    .goods{
        width: 100%;
        position: absolute;
        bottom: 0.95rem;
        top:3.5rem;
        display: flex;
        overflow: hidden;
        .menu-warp{
            flex: 0 0 1.6rem;
            background: #f3f5f7; 
            .client{
                    font-weight: 700;
                    background: #ffffff;
                }
            ul{
                li{
                    height: 1.08rem;
                    font-size: 0.24rem;
                    line-height: 0.28rem;
                    font-weight: 200;
                    padding-left: 0.24rem;
                    display: flex;
                    align-items: center;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    img{
                        width: 0.25rem;
                        height: 0.25rem;
                    }
                }
            }
        }
        .foods-warp{
            width: 100%-1.6rem;
            flex: 1;
            ul{
                li{
                    .title{
                        padding-left: 0.28rem;
                        width: 100%;
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        background-color: #f3f5f7;
                        height: 0.52rem;
                        line-height: 0.52rem;
                        border-left: 0.07rem solid #d9dde1;
                    }
                    .add{
                        position: absolute;
                        z-index: 1000;
                        right: 0.8rem;
                        width: 1.5rem;
                    }
                    .foods{
                        height: 2.2rem;
                        border-bottom: 1px solid rgba(7,17,27,0.1);
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                        padding-left: 0.36rem;
                         >img{
                           width: 1.15rem;
                           height: 1.15rem;
                           border-radius: 0.05rem; 
                        }
                        .detail{
                            padding-top: 0.05rem;
                            padding-left: 0.2rem;
                            color: rgb(147, 153, 159);
                            font-size: 0.2rem;
                            line-height: 0.4rem;
                            .name{
                               font-size: 0.28rem;
                               color: rgb(7,17,27);
                               line-height: 0.4rem;                           
                            }
                            .description{
                                width: 60%;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow:ellipsis; 
                            }
                            .price{
                                color: rgb(255,0,0);
                                font-size: 0.28rem;
                                line-height: 0.48rem;
                                font-weight: 700;
                                .old-price{
                                    color: rgb(147, 153, 159);
                                    padding-left: 0.16rem;
                                    font-size: 0.2rem;
                                    text-decoration: line-through;
                                }
                            } 
                        }
                    }
                }
            }
        }
    }
</style>



