import types from '../types';
import axios from 'axios';

let state = {
    seller:{},
    flag:false
};

let getters = {
    seller(){
        return state.seller
    },
    imgUrl(){
        return state.seller.avatar
    },
    flag(){
        return state.flag
    }
};

let actions = {
    getSeller({commit,state}){
        axios.get("./data.json").then((res)=>{
            // if(res.data.errno == 0){
                commit(types.GET_SELLER,res.data.seller)
            // }
        })
        
    },
    doQuit( { commit } ){
        commit(types.DO_QUIT)
    },
    showDetail( { commit } ){
        commit(types.SHOW_DETAIL)
    }
};

let mutations = {
    [types.GET_SELLER](state,data){
       state.seller = data;
    },
    [types.DO_QUIT](state){
        state.flag = false;
     },
    [types.SHOW_DETAIL](state){
        state.flag = true;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}


