<template>
    <div>
       <span v-for="(item,k) in starClass" :key="k" :class="[size,item]" ></span>
       <span v-if="show!=1">{{num}}</span>
    </div>
</template>

<script>
    // 定义三个常量
    const ON = "on";
    const HALF = "half";
    const OFF = "off";
    const LENGTH = 5;
    export default {
        // 从父组件获取评分，［是否显示评分］，星星大小三个参数
        props:[
            "num",
            "show",
            "size"
        ],
        computed:{
            // 将评分转换成例如［"on","on","on","half","off"］的数组
            starClass(){
                let intLength = Math.floor(this.num);
                let float = this.num-intLength;
                let result = [];
                for(let i =0 ; i<intLength ; i++){
                    result.push(ON)
                };
                if(float>=0.5){
                    result.push(HALF)
                };
                while(result.length<LENGTH) {
                    result.push(OFF);
                } 
                return result
            }
        }
    }
</script>

<style lang="less" scoped>
    span{
        display: inline-block;
        background-size: cover;
        background-repeat: no-repeat;
        color: orange;
        vertical-align: middle;
        //根据size调整大小
        &.big{
            font-size: 0.32rem;
            line-height: 0.4rem;
            width: 0.4rem;
            height: 0.4rem;
        }
        &.middle{
            font-size: 0.28rem;
            line-height: 0.3rem;
            width: 0.3rem;
            height: 0.3rem;
        }
        &.small{
            margin-right: 0.08rem;
            font-size: 0.3rem;
            line-height: 0.2rem;
            width: 0.2rem;
            height: 0.2rem;
        }
        // 根据转换后的数组显示图片
        &.on{
            background-image: url("../assets/imgs/star_on.png")
        }
        &.half{
            background-image: url("../assets/imgs/star_half.png")
        }
        &.off{
            background-image: url("../assets/imgs/star_off.png")
        }
    }
</style>
    


