<template>
  <div id="app">
    <v-header></v-header>
    <v-content></v-content> 
    <detail v-if="flag"></detail>
    <food-detail v-if="foodFlag"></food-detail>
    <cartsDetail v-if="cartsFlag" :goods="goods"></cartsDetail>
    <carts :goods="goods"></carts>
    <div class="bg" v-if="foodFlag"></div>
  </div>
</template>
<script>
  import Header from './components/header.vue';
  import Detail from "./components/detail.vue";
  import content from "./components/content.vue";
  import carts from "./components/carts.vue";
  import cartsDetail from "./components/cartsDetail.vue";
  import foodDetail from "./components/foodDetail.vue";
  import {mapGetters} from "vuex";
export default {
  name: 'app',
  components:{
    "v-header" : Header,
    "detail" : Detail,
    "v-content" : content,
    "carts" : carts,
    "cartsDetail" : cartsDetail,
    "food-detail" : foodDetail
  },
  computed:{
    ...mapGetters([
      "flag",
      "cartsFlag",
      "foodFlag",
      "goods"
    ])
  },
  mounted(){
    this.$store.dispatch("getSeller");
    this.$store.dispatch("getGoods");
  }
}
</script>

<style lang="less" scoped>
  #app{
    .bg{
      background: #ffffff;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
</style>




